.signature-pad {
  margin: auto;
  height: auto;
}

.signature-pad--body {
  padding-top: 50%;
}

.signature-pad--actions {
  overflow: hidden;
}

.signature-pad--actions > div:first-child {
  float: left;
}

.signature-pad--actions > div:last-child {
  float: right;
}
.outer {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.middle {
  display: table-cell;
  vertical-align: middle;
}